<template>
  <div class="hot">
    <div class="hot-img">
      <div>
        <div class="hot-title">
          <i>云音乐</i>
          <h1>热歌榜</h1>
          <p>更新时间</p>
        </div>
      </div>
    </div>
    <ul class="hot-ul">
      <SongList
        v-for="(item, index) in hotSongs.tracks"
        :key="item.id"
        :item="item"
        @song-id="
          $emit('song-id', $event), $emit('song-play-list', hotSongs.tracks)
        "
        :playing="playing"
        :currentId="currentId"
        class="hot-list"
        ><span>{{ (index + 1).toString().padStart(2, "0") }}</span></SongList
      >
    </ul>
  </div>
</template>

<script>
// import HotSongList from "../components/HotSongList.vue";
import SongList from "../components/SongList.vue";

export default {
  props: {
    playing: Boolean,
    currentId: Number,
  },
  data() {
    return {
      hotSongs: "",
      // SongList: "",
    };
  },
  components: {
    // HotSongList,
    SongList,
  },
  created() {
    this.axios
      .get("http://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        // console.log(res.data.playlist.tracks);
        this.hotSongs = res.data.playlist;
        // console.log(this.hotSongs.tracks);
      });
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.hot {
  position: absolute;
  top: 140px;
  left: 0px;
  width: 100%;
  height: 75%;
  overflow-y: auto;
  margin-bottom: 20px;
  .hot-img {
    width: 100%;
    height: 160px;
    background-image: url("//s3.music.126.net/mobile-new/img/hot_music_bg_3x.jpg?2bfefd3ba37eaffbd66b7d108b9de26a=");
    background-size: contain;
    background-image: no-repeat;
    .hot-title {
      padding: 27px;
      color: white;
      font-family: "宋体";
      h1 {
        font-size: 50px;
      }
    }
  }
  .hot-ul {
    .hot-list {
      margin: 0;
      padding: 10px;
      .num {
        width: 30px;
        color: #999;
        text-align: center;
        font-size: 17px;
      }
    }
  }
}
</style>
